import React,{useEffect, useState} from 'react'
import BasicKycForm from './BasicKycForm'
import BasicInfoShow from './BasicInfoShow'
import Spinner from '../../UI/Spinner'
const BasicInfoIndex = () => {
    //options-> pending , done , inReview
    const [status,setStatus] = useState('')
    useEffect(()=>{
        setStatus('pending')
    },[])
    if(status===''){
        return(
            <Spinner />
        );
    }
    if(status==='pending'){
        return(
            <BasicKycForm />
        );
    }
    if(status==='done' || status==='inReview'){
        return(
            <BasicInfoShow />
        );
    }
    
}
export default BasicInfoIndex